import get from 'lodash/get'
import b from 'bluebird'
import puppeteer from 'puppeteer'
import isint from 'wsemi/src/isint.mjs'
import isobj from 'wsemi/src/isobj.mjs'
import isarr from 'wsemi/src/isarr.mjs'
import isbol from 'wsemi/src/isbol.mjs'
import iser from 'wsemi/src/iser.mjs'
import cint from 'wsemi/src/cint.mjs'


/**
 * screenshotbase64
 *
 * @memberOf w-puppeteer-uitest
 * @param {String} url 
 * @param {Object} [opt={}] {}
 * @param {Boolean} [opt.headless=true] true
 * @param {Object} [opt.viewport={ width: 800, height: 600 }] viewport{ width: 800, height: 600 }
 * @param {Array} [opt.actions=[]] []
 * @param {Object} opt.action action
 * @param {Object} opt.action.mode 'wait','move','elemove','elehover',drag','eledrag','click','eleclick','dbclick','eledbclick','type','eletype'
 * @param {Object} opt.action action.mode'wait'{time}
 * @param {Object} opt.action action.mode'resize'{width,height}(viewport)
 * @param {Object} opt.action action.mode'move'{x1,y1}px
 * @param {Object} opt.action action.mode'elemove'{selector,nth()}selectorcssnthnthdom
 * @param {Object} opt.action action.mode'elehover'{selector}selectorcss11dom
 * @param {Object} opt.action action.mode'drag'{x1,y1,x2,y2}(x1,y1)(x2,y2)px
 * @param {Object} opt.action action.mode'eledrag'{selector,nth(),shiftx,shifty}(shiftx,shifty)selectorcssnthnthdomshiftx,shiftypx
 * @param {Object} opt.action action.mode'click'{x1,y1}px
 * @param {Object} opt.action action.mode'eleclick'{selector,nth()}selectorcssnthnthdom
 * @param {Object} opt.action action.mode'dbclick'{x1,y1}px
 * @param {Object} opt.action action.mode'eledbclick'{selector,nth()}selectorcssnthnthdom
 * @param {Object} opt.action action.mode'type'{str}str
 * @param {Object} opt.action action.mode'eletype'{selector,nth(),str,noEnter}selectorcssnthnthdomclickdomstrnoEnterenter
 * @param {Object} opt.action action.mode'keypress'{key}keykey'Backspace', 'ArrowLeft', 'ArrowRight', 'ArrowUp', 'ArrowDown'keycode
 * @param {Object} opt.action action.mode'elefocus'{selector,nth(),str}selectorcssnthnthdomdom
 * keypress
 * @param {Integer} [opt.waitsec=5] 5
 * @returns {String} screenshotbase64
 */
async function getB64(url, opt = {}) {


    //headless
    let headless = get(opt, 'headless')
    if (!isbol(headless)) {
        headless = true
    }


    //viewport
    let viewport = get(opt, 'viewport')
    if (!isobj(viewport)) {
        viewport = {
            x: 0,
            y: 0,
            width: 800,
            height: 600,
        }
    }


    //action
    let actions = get(opt, 'actions')
    if (!isarr(actions)) {
        actions = []
    }


    //waitsec
    let waitsec = get(opt, 'waitsec')
    if (!isint(waitsec)) {
        waitsec = 5
    }


    //new page
    let olaunch = {
        //timeout: 0,
        headless: headless,
        slowMo: 20,
        // arg: [
        //     //`--window-size=${viewport.width},${viewport.height}`, //
        //     //`--disable-resize-lock`, //
        // ],
        // defaultViewport: { //
        //     x: 0,
        //     y: 0,
        //     width: 800,
        //     height: 600,
        // }
    }
    let browser = await puppeteer.launch(olaunch)
    let page = await browser.newPage()


    //show page
    await page.goto(url)
    await page.setViewport(viewport)
    // await page.emulate({ //
    //     viewport: {
    //         width: viewport.width,
    //         height: viewport.height,
    //     },
    //     userAgent: ''
    // })

    //getxy
    async function getxy(selector, n = 0) {

        //n
        n = cint(n)

        //ele
        let ele
        if (n === 0) {
            ele = await page.$(selector)
            if (iser(ele)) {
                console.log('page.$ get no result: ' + selector)
            }
        }
        else {
            let eles = await page.$$(selector)
            if (eles.length === 0) {
                console.log('page.$$ get no result: ' + selector)
            }
            ele = eles[n]
        }

        //r
        if (!ele.boundingBox) {
            console.log('ele.boundingBox get no result: ' + selector)
        }
        let r = await ele.boundingBox()
        r['cx'] = r.x + r.width / 2
        r['cy'] = r.y + r.height / 2
        r['ele'] = ele
        // let r = await page.evaluate((e) => {
        //     let { top, left, bottom, right } = e.getBoundingClientRect()
        //     return {
        //         top: top,
        //         left: left,
        //         bottom: bottom,
        //         right: right,
        //         cx: (left + right) / 2,
        //         cy: (top + bottom) / 2,
        //     }
        // }, ele)

        return r
    }


    //wait
    await page.waitFor(waitsec * 1000)


    //action
    await b.mapSeries(actions, async function(v) {
        if (v.mode === 'wait') {
            await page.waitFor(v.time)
        }
        else if (v.mode === 'resize') {
            await page.waitFor(300)
            await page.setViewport({
                x: 0,
                y: 0,
                width: v.width,
                height: v.height,
            })
            await page.waitFor(300)
        }
        else if (v.mode === 'move') {
            await page.waitFor(300)
            await page.mouse.move(v.x1, v.y1)
            await page.waitFor(300)
        }
        else if (v.mode === 'elemove') {
            await page.waitFor(300)
            let r = await getxy(v.selector, v.nth)
            await page.mouse.move(r.cx, r.cy)
            await page.waitFor(300)
        }
        else if (v.mode === 'elehover') {
            await page.waitFor(300)
            await page.hover(v.selector)
            await page.waitFor(300)
        }
        else if (v.mode === 'drag') {
            await page.waitFor(300)
            await page.mouse.move(v.x1, v.y1)
            await page.mouse.down()
            await page.waitFor(300)
            await page.mouse.move(v.x2, v.y2, { steps: 50 })
            await page.waitFor(300)
            await page.mouse.up()
            await page.waitFor(300)
        }
        else if (v.mode === 'eledrag') {
            await page.waitFor(300)
            let r = await getxy(v.selector, v.nth)
            await page.mouse.move(r.cx, r.cy)
            await page.mouse.down()
            await page.waitFor(300)
            await page.mouse.move(r.cx + v.shiftx, r.cy + v.shifty, { steps: 50 })
            await page.waitFor(300)
            await page.mouse.up()
            await page.waitFor(300)
        }
        else if (v.mode === 'click') {
            await page.waitFor(300)
            await page.mouse.move(v.x1, v.y1)
            await page.mouse.down()
            await page.mouse.up()
            await page.waitFor(300)
        }
        // else if (v.mode === 'eleclickB') {
        //     await page.waitFor(300)
        //     await page.click(v.selector)
        //     await page.waitFor(300)
        // }
        else if (v.mode === 'eleclick') {
            await page.waitFor(300)
            let r = await getxy(v.selector, v.nth)
            await r.ele.click()
            // await page.mouse.move(r.cx, r.cy)
            // await page.waitFor(50)
            // await page.mouse.down()
            // await page.mouse.up()
            await page.waitFor(300)
        }
        else if (v.mode === 'dbclick') {
            await page.waitFor(300)
            await page.mouse.click(v.x1, v.y1, { clickCount: 2 })
            await page.waitFor(300)
        }
        // else if (v.mode === 'eledbclickB') {
        //     await page.waitFor(300)
        //     await page.click(v.selector, { clickCount: 2 })
        //     await page.waitFor(300)
        // }
        else if (v.mode === 'eledbclick') {
            await page.waitFor(300)
            let r = await getxy(v.selector, v.nth)
            await r.ele.click({ clickCount: 2 })
            //await page.mouse.move(r.cx, r.cy)
            // //await page.waitFor(50)
            // await page.mouse.down()
            // await page.mouse.move(r.cx, r.cy)
            // await page.mouse.up()
            // //await page.waitFor(50)
            // await page.mouse.move(r.cx, r.cy)
            // await page.mouse.down()
            // await page.mouse.move(r.cx, r.cy)
            // await page.mouse.up()
            await page.waitFor(300)
        }
        else if (v.mode === 'type') {
            await page.waitFor(300)
            await page.keyboard.type(v.str, { delay: 50 })
            await page.keyboard.type(String.fromCharCode(13))
            await page.waitFor(300)
        }
        else if (v.mode === 'eletype') {
            await page.waitFor(300)
            let r = await getxy(v.selector, v.nth)
            await r.ele.click()
            await page.waitFor(50)
            await page.keyboard.type(v.str, { delay: 50 })
            if (!v.noEnter) {
                await page.keyboard.type(String.fromCharCode(13))
            }
            await page.waitFor(300)
        }
        else if (v.mode === 'keypress') {
            await page.waitFor(300)
            await page.keyboard.press(v.key) //Backspace, ArrowLeft, ArrowRight, ArrowUp, ArrowDown
            await page.waitFor(300)
        }
        else if (v.mode === 'elefocus') {
            await page.waitFor(300)
            let r = await getxy(v.selector, v.nth)
            await r.ele.focus()
            await page.waitFor(300)
        }
        else {
            console.log('mode is unrecognized: ' + v.mode)
        }
        // else if (v.mode === 'press') {
        //     await page.waitFor(300)
        //     //await page.keyboard.press(v.key, { delay: 50 })
        //     //await page.keyboard.sendCharacter(String.fromCharCode(9))
        //     //await page.keyboard.press(String.fromCharCode(9))
        //     //await page.keyboard.type('\t')
        // }
    })


    //wait
    await page.waitFor(2000) //2saction


    // //get base64 from screenshot, sceenshottooltip, (:ag-grid)
    // //add html2canvas and html2pic64
    // await page.addScriptTag({ url: 'https://html2canvas.hertzen.com/dist/html2canvas.min.js' })
    // let cfun = `
    // function html2pic64(ele) {
    //     //print screenhtmlbase64

    //     return new Promise((resolve, reject) => {
    //         let opt={
    //             logging:false, //console.log
    //             scale:1, //anti-aliasing
    //             width:${viewport.width},
    //             height:${viewport.height},
    //         }
    //         html2canvas(ele, opt)
    //             .then(function (canvas) {
    //                 resolve(canvas.toDataURL('image/png'))
    //             })
    //             .catch(function (msg) {
    //                 reject(msg)
    //             })
    //     })
    // }
    // `
    // await page.addScriptTag({ content: cfun })
    // let base64 = await page.evaluate(() => {
    //     return html2pic64(document.body)
    // })
    // base64 = base64.replace('data:image/png;base64,', '') //img


    //get base64 from screenshot, , screenshotresizedpi(:96), tooltip
    //
    let base64 = await page.screenshot({ encoding: 'base64' }) //fullPage: true


    //close
    await page.close()
    await browser.close()


    return base64
}


export default getB64
