import get from 'lodash/get'
import b from 'bluebird'
import fs from 'fs'
import ispint from 'wsemi/src/ispint.mjs'
import cint from 'wsemi/src/cint.mjs'
import getB64 from './getB64.mjs'


/**
 * (screenshot)
 *
 * @memberOf w-puppeteer-uitest
 * @param {Array} items html
 * @param {String} item.name item
 * @param {String} item.url item
 * @param {Array} item.actions item
 * @param {String} item.fn_png itempng
 * @param {String} item.fn_base64 itembase64
 * @param {Object} [optExp={}] {}
 * @param {Boolean} [optExp.headless=true] true
 * @param {Object} [optExp.viewport={ width: 800, height: 600 }] viewport{ width: 800, height: 600 }
 * @param {Number} [optExp.num_web=10] 10
 * @returns {Promise} Promiseresolverejectitem
 */
async function expBuild(items, optExp = {}) {

    //headless, viewport, waitsec, num_web
    let headless = get(optExp, 'headless')
    let viewport = get(optExp, 'viewport')
    let waitsec = get(optExp, 'waitsec')
    let num_web = 10
    if (ispint(optExp.num_web)) {
        num_web = cint(optExp.num_web)
    }

    //mapSeries , map 
    return b.map(items, async function (v) {
        console.log('expBuild: ' + v.name)

        //opt
        let opt = {
            headless,
            viewport,
            actions: v.actions,
            waitsec,
        }

        //getB64
        let b64 = await getB64(v.url, opt)

        //save
        if (v.fn_png) {
            fs.writeFileSync(v.fn_png, b64, 'base64')
        }
        if (v.fn_base64) {
            fs.writeFileSync(v.fn_base64, b64, 'utf8')
        }

        console.log('success: ' + v.name)
    }, { concurrency: num_web })
        .then(() => {
            console.log('\x1b[32m%s\x1b[0m', 'expBuild success')
        })
        .catch((msg) => {
            console.log('\x1b[31m%s\x1b[0m', 'expBuild error: ' + msg)
        })

}


export default expBuild
